@charset "UTF-8";
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.benefit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 24px 18px 26px 18px;
  text-align: center;
  text-transform: uppercase;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .benefit {
    justify-content: flex-start;
    padding: 42px 18px 41px 18px;
    border-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .benefit {
    padding: 54px 18px 50px 18px;
  }
}
.benefit::after {
  content: "";
  position: relative;
  top: 25px;
  display: block;
  width: 100%;
  border-bottom: 1px solid #d6d6d6;
}
@media (min-width: 768px) {
  .benefit::after {
    display: none;
  }
}
.benefit:last-child::after, .benefit--wide::after, .benefit--feature::after {
  border-bottom: 0;
}
.benefit--wide {
  padding: 0;
  border-bottom: 0;
}
@media (min-width: 768px) {
  .benefit--wide {
    grid-column: span 3;
  }
}
.benefit--clipped {
  padding-top: 18px;
  background-color: #81b3d2;
  clip-path: polygon(50% 3.5%, 100% 0, 100% 100%, 0 100%, 0 0);
}
@media (min-width: 768px) {
  .benefit--clipped {
    padding-top: 0;
    clip-path: none;
  }
}
.benefit--feature {
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 17px 18px 3px 18px;
  background-color: transparent;
  border-bottom: 0;
}
.benefit--feature .benefit__title {
  margin: 14px 0 16px 0;
}
@media (min-width: 768px) {
  .benefit--feature .benefit__title {
    margin: 20px 0 23px 0;
  }
}
@media (min-width: 768px) {
  .benefit--feature {
    flex-direction: column;
    align-items: center;
    padding: 50px 44px 60px 44px;
  }
}
@media (min-width: 1200px) {
  .benefit--feature {
    padding: 60px 33px 69px 33px;
  }
}
.benefit--accommodation::before, .benefit--cuisine::before, .benefit--souvenirs::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  margin-right: 7px;
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 768px) {
  .benefit--accommodation::before, .benefit--cuisine::before, .benefit--souvenirs::before {
    width: 80px;
    height: 80px;
    margin-right: 0;
  }
}
.benefit--accommodation::before {
  margin-top: 8px;
  background-image: url("../img/icons/icon-house.svg");
}
.benefit--cuisine::before {
  margin-top: 5px;
  background-image: url("../img/icons/icon-food.svg");
}
.benefit--souvenirs::before {
  margin-top: 4px;
  background-image: url("../img/icons/icon-gift.svg");
}
@media (min-width: 1200px) {
  .benefit--souvenirs::before {
    margin-top: 10px;
  }
}
.benefit--souvenirs {
  margin-bottom: 41px;
}
@media (min-width: 768px) {
  .benefit--souvenirs {
    margin-bottom: 0;
  }
}

.benefit__figure {
  display: grid;
  width: 100%;
  margin: 0;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .benefit__figure {
    display: grid;
    grid-auto-flow: dense;
    grid-template-columns: 1fr 2fr;
  }
}
@media (min-width: 768px) {
  .benefit__figure--reversed {
    grid-template-columns: 2fr 1fr;
  }
}
@media (min-width: 1200px) {
  .benefit__figure--reversed .benefit__description {
    width: 70%;
  }
}

.benefit__figcaption {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 35px 18px 39px 18px;
  color: #ffffff;
  background-color: #81b3d2;
}
.benefit__figcaption .benefit__description {
  max-width: 270px;
}
@media (min-width: 768px) {
  .benefit__figcaption .benefit__title {
    max-width: 150px;
  }
}
@media (min-width: 768px) {
  .benefit__figcaption {
    padding: 52px 33px 31px 33px;
  }
}
@media (min-width: 768px) {
  .benefit__figure--reversed .benefit__figcaption {
    grid-column: 2/3;
  }
}

.benefit__number::before,
.benefit__number::after {
  content: " — ";
}

.benefit__title {
  margin: 0 0 11px 0;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .benefit__title {
    max-width: 158px;
    margin: 0 0 14px 0;
    font-size: 21px;
  }
}

.benefit__number {
  display: inline-block;
  margin: 0 0 17px 0;
}
@media (min-width: 768px) {
  .benefit__number {
    margin: 0 0 23px 0;
  }
}

.benefit__image {
  display: block;
  width: 100%;
  min-height: 260px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .benefit__image {
    min-height: 256px;
  }
}
@media (min-width: 768px) {
  .benefit__figure--reversed .benefit__image {
    grid-column: 1/2;
  }
}

.benefit__description {
  max-width: 255px;
  margin: 0;
}
.benefit__description br {
  display: none;
}
@media (min-width: 768px) {
  .benefit__description br {
    display: inline;
  }
}
@media (min-width: 768px) {
  .benefit__description {
    max-width: 200px;
  }
}
@media (min-width: 1200px) {
  .benefit__description {
    max-width: 290px;
  }
}
.benefit--feature .benefit__description {
  box-sizing: content-box;
  padding-left: 47px;
  text-align: left;
}
@media (min-width: 768px) {
  .benefit--feature .benefit__description {
    box-sizing: border-box;
    padding-left: 0;
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .benefit--feature .benefit__description {
    max-width: 300px;
  }
}

@media (min-width: 1200px) {
  .benefits {
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }
}

.benefits__heading {
  padding: 32px 0 19px 0;
}
@media (min-width: 768px) {
  .benefits__heading {
    margin: 0 124px;
    padding: 47px 0 41px 0;
  }
}
@media (min-width: 1200px) {
  .benefits__heading {
    margin: 0 357px;
    padding: 60px 0 28px 0;
  }
}

.benefits__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .benefits__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.button {
  display: inline-block;
  max-width: 100%;
  padding: 16px 49px 16px 49px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: inherit;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: 0;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e7e5e5;
}
.button--small-text {
  font-size: 18px;
}
@media (min-width: 768px) {
  .button--small-text {
    font-size: 21px;
  }
}
.button--icon {
  padding: 16px 0 16px 0;
}

.cta {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  .cta {
    padding-top: 21px;
  }
}

.cta__heading {
  margin-top: 7px;
  padding: 31px 0 18px 0;
}
@media (min-width: 768px) {
  .cta__heading {
    padding: 31px 80px 18px 80px;
  }
}

.cta__link {
  width: calc(100% - 36px);
  max-width: 458px;
  margin: 0 18px 49px 18px;
}
@media (min-width: 768px) {
  .cta__link {
    margin: 13px 18px 55px 18px;
  }
}
@media (min-width: 1200px) {
  .cta__link {
    margin: 13px 18px 68px 18px;
  }
}

.feedback__heading {
  padding: 32px 0 12px 0;
}
@media (min-width: 768px) {
  .feedback__heading {
    padding: 32px 0 49px 0;
  }
}
@media (min-width: 1200px) {
  .feedback__heading {
    padding: 49px 0 10px 0;
  }
}

.form {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .form {
    padding: 0 120px;
  }
}

.form__fieldset {
  position: relative;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 22px 0 8px 0;
  border: 0;
}
.form__fieldset:first-child {
  padding: 0 0 24px 0;
}
@media (min-width: 768px) {
  .form__fieldset:first-child {
    padding: 16px 0 24px 0;
  }
}
.form__fieldset::before {
  content: "";
  position: absolute;
  left: -20px;
  width: 100vw;
  height: 1px;
  background-color: #dbdbdb;
}
@media (min-width: 768px) {
  .form__fieldset::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .form__fieldset {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 15px 0 24px 0;
    border-top: 1px solid #dbdbdb;
  }
}
.form__fieldset--half {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 768px) {
  .form__fieldset--half {
    width: calc(50% - 28px);
    margin-bottom: 39px;
    border-top: 0;
  }
}
@media (min-width: 1200px) {
  .form__fieldset--half {
    margin-bottom: 46px;
  }
}
@media (min-width: 768px) {
  .form__fieldset--half:nth-child(even) {
    padding-left: 28px;
  }
}
@media (min-width: 1200px) {
  .form__fieldset--half:nth-child(even) {
    padding-left: 51px;
  }
}
@media (min-width: 768px) {
  .form__fieldset--half:first-child {
    position: relative;
    padding-right: 28px;
  }
}
@media (min-width: 1200px) {
  .form__fieldset--half:first-child {
    padding-right: 51px;
  }
}
@media (min-width: 768px) {
  .form__fieldset--half:first-child::after {
    content: "";
    position: absolute;
    top: -55px;
    right: 0;
    height: calc(100% + 40px);
    border-right: 1px solid #dbdbdb;
  }
}

.form__legend {
  float: left;
  width: 100%;
  margin: 24px auto 20px auto;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .form__legend {
    float: none;
    width: 400px;
    margin: 24px auto 13px auto;
    padding: 0 45px;
    font-size: 21px;
  }
}
.form__fieldset--half .form__legend {
  width: auto;
}
@media (min-width: 768px) {
  .form__fieldset--half .form__legend {
    margin: 0 0 11px 0;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .form__fieldset--half .form__legend {
    margin: 0 0 12px 0;
    padding: 0;
  }
}

.form__group {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
@media (min-width: 768px) {
  .form__group {
    display: grid;
    width: 100%;
    gap: 16px;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .form__group {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .form__group--radio {
    margin-bottom: 25px;
    padding: 0 36px;
  }
}
@media (min-width: 1200px) {
  .form__group--radio {
    margin-top: 17px;
    margin-bottom: 51px;
    padding: 0 54px;
    gap: 63px;
  }
}
@media (min-width: 768px) {
  .form__group--checkbox {
    margin-bottom: 18px;
    padding: 0 84px;
    column-gap: 31px;
  }
}
@media (min-width: 1200px) {
  .form__group--checkbox {
    margin-top: 30px;
    margin-bottom: 64px;
    padding: 0 45px;
    gap: 64px;
  }
}

.form__submit {
  width: 100%;
  margin-top: 5px;
  text-align: center;
}
@media (min-width: 768px) {
  .form__submit {
    width: 50%;
    margin-top: 0;
    margin-bottom: 77px;
  }
}
@media (min-width: 1200px) {
  .form__submit {
    max-width: 384px;
    margin-left: 96px;
  }
}

.form__hint {
  display: block;
  margin-bottom: 31px;
  font-size: 12px;
  line-height: 21px;
  text-align: right;
  color: rgba(51, 51, 51, 0.6);
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .form__hint {
    margin-top: 23px;
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  .form__hint {
    margin-right: 96px;
  }
}

.form-input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.form-input:not(:last-child) {
  margin-bottom: 16px;
}

.form-input__label {
  padding-left: 16px;
  text-transform: uppercase;
}

.form-input__input {
  width: 100%;
  max-width: 430px;
  padding: 16px;
  font-weight: 700;
  font-size: 16px;
  line-height: 26px;
  background-color: #f2f2f2;
  border: 0;
  transition: background-color 0.3s ease;
}
.form-input__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.form-input__input--textarea {
  align-self: center;
  max-width: 768px;
  height: 150px;
  margin-bottom: 33px;
  padding: 18px 16px;
  font-size: 18px;
  line-height: 26px;
  font-family: inherit;
  resize: none;
}
@media (min-width: 768px) {
  .form-input__input--textarea {
    height: 160px;
    margin-bottom: 23px;
  }
}
.form-input__input--phone, .form-input__input--email {
  padding-right: 65px;
  background-repeat: no-repeat;
  background-position: right;
}
.form-input__input--phone {
  background-image: url("../img/icons/icon-phone.svg");
}
.form-input__input--email {
  background-image: url("../img/icons/icon-mail.svg");
}
.form-input__input:hover {
  background-color: #e5e5e5;
}
.form-input__input:focus {
  background-color: #ffffff;
  outline: #e5e5e5 solid 4px;
  outline-offset: -4px;
}

.form-option {
  display: flex;
  align-items: center;
  max-width: 100%;
  gap: 16px;
}

.form-option__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form-option__icon {
  position: relative;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.form-option__icon:hover {
  background-color: #e5e5e5;
}
.form-option__icon:active {
  background-color: #d5d5d5;
}
.form-option__icon:active.form-option__icon--radio::after, .form-option__icon:active.form-option__icon--checkbox::after {
  opacity: 0.3;
}

.form-option__icon--radio {
  border-radius: 50%;
}

.form-option__input:checked ~ .form-option__icon--radio::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.form-option__icon--checkbox {
  border-radius: 4px;
}

.form-option__input:checked ~ .form-option__icon--checkbox::after {
  content: url("../img/icons/icon-tick.svg");
  position: absolute;
  top: 50%;
  left: 50%;
  width: 22px;
  height: 16px;
  transform: translate(-50%, -50%);
}

.gallery {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .gallery {
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .gallery {
    padding: 0;
  }
}

.gallery__heading {
  padding: 32px 0 31px 0;
}

.gallery__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .gallery__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .gallery__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.gallery__item--large {
  grid-column: 1/-1;
}
@media (min-width: 1200px) {
  .gallery__item--large .gallery__figcaption {
    padding-left: 21px;
  }
}
@media (min-width: 768px) {
  .gallery__item {
    padding: 0;
  }
}
.gallery__item::before, .gallery__item:last-of-type::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100vw;
  height: 1px;
  background-color: #dbdbdb;
}
@media (min-width: 768px) {
  .gallery__item::before, .gallery__item:last-of-type::after {
    display: none;
  }
}

.gallery__figure {
  margin: 0;
}
@media (min-width: 768px) {
  .gallery__figure {
    background-color: #f2f2f2;
  }
}
@media (min-width: 1200px) {
  .gallery__figure {
    position: relative;
  }
}

.gallery__link {
  display: block;
}

.gallery__image {
  display: block;
  width: 100%;
  margin-bottom: 15px;
  padding-top: 24px;
  object-fit: cover;
}
.gallery__item--large .gallery__image {
  max-height: 400px;
}
@media (min-width: 768px) {
  .gallery__image {
    margin-bottom: 0;
    padding-top: 0;
  }
}

.gallery__figcaption {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  padding-bottom: 28px;
  gap: 11px;
}
@media (min-width: 768px) {
  .gallery__figcaption {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 22px 20px 12px 20px;
  }
}
@media (min-width: 1200px) {
  .gallery__figcaption {
    position: absolute;
    bottom: 0;
    z-index: 1;
    padding: 22px 20px 12px 25px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.gallery__info {
  margin-bottom: 2px;
}

.gallery__subtitle {
  margin: 0 0 2px 0;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.gallery__author {
  width: 100%;
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 21px;
}

.gallery__like-button {
  position: relative;
  display: flex;
  align-items: flex-end;
  padding: 0;
  font-weight: 700;
  font-size: 14px;
  font-family: inherit;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  transition: transform 0.1s ease;
  gap: 14px;
}
.gallery__like-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  background-color: transparent;
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
@media (min-width: 768px) {
  .gallery__like-button {
    align-self: flex-start;
    margin-top: 7px;
    gap: 9px;
  }
}
@media (min-width: 1200px) {
  .gallery__like-button {
    color: #ffffff;
  }
}
.gallery__like-button:hover::after {
  background-color: rgba(0, 0, 0, 0.5);
}
.gallery__like-button:active {
  transform: translateY(2px);
}

.gallery__like-icon {
  opacity: 0.3;
}
@media (min-width: 1200px) {
  .gallery__like-icon {
    opacity: 1;
  }
}

.heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 275px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .heading {
    max-width: 600px;
  }
}
@media (min-width: 768px) {
  .heading--inner {
