.nav-button {
  position: relative;
  margin-right: 20px;
  border: 0;
  cursor: pointer;
}
.nav-button, .nav-button::before, .nav-button::after {
  z-index: 3;
  display: inline-block;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  transition: transform 0.3s ease-in, background-color 0.3s ease-in;
}
.nav-button::before, .nav-button::after {
  content: "";
  position: absolute;
  left: 0;
}
.nav-button::before {
  top: 9px;
}
.nav-button::after {
  top: -9px;
}
.nav-button:hover, .nav-button:hover::before, .nav-button:hover::after {
  background-color: rgba(129, 179, 210, 0.6);
}
.nav-button:active, .nav-button:active::before, .nav-button:active::after {
  background-color: rgba(129, 179, 210, 0.3);
}
.no-js .nav-button {
  display: none;
}
@media (min-width: 768px) {
  .nav-button {
    display: none;
  }
}

.is-nav-open .nav-button {
  background-color: rgba(255, 255, 255, 0);
}
.is-nav-open .nav-button::before, .is-nav-open .nav-button::after {
  top: 0;
  background-color: #ffffff;
}
.is-nav-open .nav-button::before {
  transform: rotate(135deg);
}
.is-nav-open .nav-button::after {
  transform: rotate(-135deg);
}
.is-nav-open .nav-button:hover::before, .is-nav-open .nav-button:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.is-nav-open .nav-button:active::before, .is-nav-open .nav-button:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}

